<template>
    <component :vo="this.vo" :is="component"></component>
</template>

<script>
export default {
  name: 'AnswerLoader',
  data () {
    return {
      component: null
    }
  },
  props: {
    typeName: {type: String, required: true, default: null},
    vo: {required: true, default: null}
  },
  watch: {
    typeName () {
      this.component = () => this.loadAnswer()
    }
  },
  computed: {
    loadAnswer () {
      const component = String(this.typeName) === '0' ? 'SelectAnswer' : 'TextAnswer'
      return () => import(`@/components/survey/management/space/answer/${component}`)
    }
  },
  created () {
    if (String(this.typeName)) this.component = () => this.loadAnswer()
  }
}
</script>

<style scoped>
</style>



