<template>
  <div class="work-space">
      <ul>
        <li>
           <!-- 등록, 수정 -->
          <div v-if="$route.meta.mode !== 'detail'">
            <input type="text" v-model="srvMtritems.srvMtritemsTitle" placeholder="질문"/>
            <select v-model="srvMtritems.srvMtritemsType">
              <option value="">선택</option>
              <option value="0">객관식</option>
              <option value="1">주관식</option>
            </select>
          </div>
          <!-- 상세 -->
          <div v-else>
            <input type="text" v-model="srvMtritems.srvMtritemsTitle" placeholder="질문" readonly/>
            <select v-model="srvMtritems.srvMtritemsType" disabled>
              <option value="">선택</option>
              <option value="0">객관식</option>
              <option value="1">주관식</option>
            </select>
          </div>
        </li>
        <li>
          <AnswerLoader :vo="srvMtritems" :typeName="srvMtritems.srvMtritemsType" v-on:setAnswerItem="setAnswerItem"/>
        </li>
      </ul>
  </div>
</template>

<script>
import AnswerLoader from '@/components/survey/management/space/answer/AnswerLoader.vue'
export default {
  name: 'Workspace',
  props: ['vo', 'index', 'isLast'],
  components: {
    AnswerLoader
  },
  data () {
    return {
      srvMtritems: {
        srvMstrId: '',
        srvMtritemsTitle: '',
        srvMtritemsType: '',
        srvItems: []
      }
    }
  },
  created () {
    // 수정, 상세화면 일때
    if (this.vo) {
      this.srvMtritems = this.vo
    }
  },
  updated () {
    this.$emit('childUpdated', this.srvMtritems, this.index)
  },
  methods: {
    // 콤포넌트 변경 함수
    settingChange (typeName) {
      this.typeName = typeName
    },
    // 응답문항 set
    setAnswerItem (srvItems) {
      this.srvMtritems.srvItems = JSON.stringify(srvItems)
      this.$forceUpdate()
    }
  }
}
</script>

<style scoped>
  div.work-space {
    border: 1px solid;
    width: 40em;
    height: auto;
    margin: 10px 0px 10px 0px;
  }
  div.work-space ul {
    list-style: none;
    padding: 30px;
  }
  div.work-space ul li input {
    width: 32em;
    padding: 10px;
    margin: 0px;
  }
   div.work-space ul li select {
    width: 7.5em;
    padding: 10px;
    vertical-align: middle;
  }
</style>



